.container {
    /* border: rgba(177, 175, 175, 0.781) solid 1px; */
    /* margin-left: 0; */
    justify-content: flex-end;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-gap: 10px;
  }
  
  .listgroup {
    display: grid;
    grid-template-columns: 3fr 1fr 1fr;
    padding: 10px;
    align-items: center;
    font-weight: 600;
    font-family: "Raleway";
  }
  
  #importance {
    width: fit-content;
    height: fit-content;
    border-radius: 40%;
    padding: 2px;
    font-size: 0.7em;
    color: white;
    font-weight: 600;
    text-transform: uppercase;
  }
  
  .noflex :target li {
    display: inline;
  }
  #number {
    font-weight: 600;
  }
  
  .noflex li {
    list-style: none;
    font-size: 0.6em;
    font-weight: 500;
    color: rgb(78, 75, 75);
    text-align: left;
  }
  
  #more {
    color: blue;
    font-size: 13px;
    align-self: center;
  }
  
  .container-head {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-container {
    padding: 15px;
    border: rgba(128, 128, 128, 0.178) solid 1px;
    border-radius: 7px;
  }
  .header-list {
    font-size: 14px;
    color: #303136;
  }
  