.Navbar {
    font-weight: 600;
    width: 100%;
    padding: 7px 5px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    box-shadow: -1px -3px 6px 0px hsla(0, 0%, 45.1%, 0.5);
  }
  
  .tabs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
  }
  .tabs a {
    margin-right: 1em;
  }
  
  .logo {
    width: 7%;
    align-content: center;
  }
  .logo img {
    margin: 0 1em;
    width: 100%;
    align-self: center;
  }
  .nav-right {
    display: flex;
    justify-content: space-between;
  }
  #rk {
    margin: 0 1em;
    color: white;
    background-color: #747474;
    border-radius: 50%;
    padding: 1px;
    align-self: center;
  }
  