.activityContainer {
    justify-content: flex-end;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 10px;
  }
  
  #listgroupActivity {
    display: grid;
    grid-template-columns: 30px 1fr;
    padding: 10px;
    align-items: center;
    font-weight: 600;
    font-family: "Raleway";
  }
  
  #number {
    font-weight: 500;
    font-size: 12px;
    color: gray;
  }
  
  #more {
    color: blue;
    font-size: 13px;
    align-self: center;
  }
  
  .container-head {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
  .main-container {
    padding: 15px;
    border: rgba(128, 128, 128, 0.178) solid 1px;
    border-radius: 7px;
  }
  .header-list {
    font-size: 14px;
    color: #303136;
  }
  